export function toVietnameseWords(number: number): string {
    if (isNaN(number)) return "Kh√¥ng ph·∫£i s·ªë h·ª£p l·ªá";
    
    const units = ["", "m·ªôt", "hai", "ba", "b·ªën", "nƒÉm", "s√°u", "b·∫£y", "t√°m", "ch√≠n"];
    const tens = ["l·∫ª", "m∆∞·ªùi", "hai m∆∞∆°i", "ba m∆∞∆°i", "b·ªën m∆∞∆°i", "nƒÉm m∆∞∆°i", "s√°u m∆∞∆°i", "b·∫£y m∆∞∆°i", "t√°m m∆∞∆°i", "ch√≠n m∆∞∆°i"];
    const scales = ["", "ngh√¨n", "tri·ªáu", "t·ª∑", "ngh√¨n t·ª∑", "tri·ªáu t·ª∑"];

    let result = "";
    let numberStr = number?.toString();
    let groups = [];

    // Chia s·ªë th√†nh t·ª´ng nh√≥m ba ch·ªØ s·ªë
    while (numberStr?.length > 0) {
        groups.unshift(numberStr.slice(-3));
        numberStr = numberStr.slice(0, -3);
    }

    // X·ª≠ l√Ω t·ª´ng nh√≥m ba ch·ªØ s·ªë
    for (let i = 0; i < groups.length; i++) {
        let group = parseInt(groups[i]);

        if (group === 0) continue;

        let [hundreds, tensDigit, unitsDigit] = groups[i].padStart(3, "0").split("").map(Number);

        let groupText = "";

        if (hundreds) {
            groupText += units[hundreds] + " trƒÉm ";
        }

        if (tensDigit > 1) {
            groupText += tens[tensDigit] + " ";
            if (unitsDigit !== 0) {
                groupText += (unitsDigit === 5 ? "lƒÉm" : units[unitsDigit]) + " ";
            }
        } else if (tensDigit === 1) {
            groupText += "m∆∞·ªùi ";
            if (unitsDigit !== 0) {
                groupText += (unitsDigit === 5 ? "lƒÉm" : units[unitsDigit]) + " ";
            }
        } else if (unitsDigit !== 0) {
            groupText += (hundreds ? "l·∫ª " : "") + (unitsDigit === 5 ? "nƒÉm" : units[unitsDigit]) + " ";
        }

        result += groupText + scales[groups.length - 1 - i] + " ";
    }

    return result.trim() + " ƒê·ªìng";
}